import React, { ReactElement } from "react"

import { Box, BoxProps } from "./box"
import { Text } from "./text"
import { Button } from "./buttons/button"

import { t } from "../i18n/i18n"

interface Props {
    titleText?: string
    buttonText?: string
    boxStyle?: BoxProps
    titleStyle?: BoxProps
    textStyle?: BoxProps
    buttonStyle?: BoxProps
}

export const HeaderContent = ({
    titleText,
    buttonText,
    boxStyle,
    titleStyle,
    textStyle,
    buttonStyle
}: Props): ReactElement => (
    <Box
        pl={["30px", "70px", "189px"]} 
        pr={["30px", 0]}
        pt={["5px", "5px", "69px"]}
        width={["auto", "auto", "584px"]}
        {...boxStyle}
    >
        <Text
            fontWeight="900"
            fontSize={[6, 8]}
            lineHeight={["32px", "32px", "45px"]}
            textAlign={["center", "left"]}
            color="primary"
            mb="11px"
            {...titleStyle}
        >
            {t(titleText || "header.title")}
        </Text>
        <Text
            fontSize={4}
            textAlign={["center", "left"]}
            lineHeight="34px"
            color="text.1"
            mt="11px"
            mb="28px"
            {...textStyle}
        >
            {t("header.text")}
        </Text>

        {buttonText !== "" && <Button
            variant="primary"
            width={["100%", "171px"]}
            py="8px"
            mb="11px"
            text={t(buttonText || "header.action")}
            {...buttonStyle}
        />}
    </Box>
)
