import React, { ReactElement } from "react"
import { Formik, Form } from "formik"
import { SliderInput } from "./slider-input"
import { InputField } from "../../ui/inputs/input-field"
import { Button } from "../../ui/buttons/button"
import { t } from "../../i18n/i18n"
import { Box } from "../../ui/box"
import { MIN_RANGE, MAX_RANGE } from "../../core/constants"

interface Props {
  isMobile: boolean
}

export const HealthHeaderFields = ({ isMobile }: Props): ReactElement => {
  const initialValues = {
    min: 0,
    max: 1000,
  }

  const handleSubmit = (): void => alert("submitted")

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={handleSubmit}
      component={({ values, setFieldValue }): ReactElement => (
        <Form>
          <Box
            display="flex"
            flexDirection="column"
            alignItems="end"
            width="100%"
          >
            <SliderInput
              minName="min"
              maxName="max"
              minValue={MIN_RANGE}
              maxValue={MAX_RANGE}
              values={values}
              setFieldValue={setFieldValue}
            />

            <InputField
              name="date"
              type="date"
              width="100%"
              boxProps={{ width: "100%" }}
            />

            <Button
              text={t("general.continue")}
              width={isMobile ? "100%" : "185px"}
              ml="auto"
              mt="15px"
            />
          </Box>
        </Form>
      )}
    />
  )
}
